import RestofySource from '../../data/restofy-source';
import UrlParser from '../../routes/url-parser';
import LikeButtonInitiator from '../../utils/like-button-initiator';
import ReviewInitiator from '../../utils/review-initiator';
import '../../component/detail-item';
import '../../component/review-item';

const Detail = {
  async render() {
    return `
      <div id="detail">
        <p class="text-center">Loading Content...</p>
      </div>
    `;
  },

  async afterRender() {
    const url = UrlParser.parseActiveUrlWithoutCombiner();

    const detailContainer = document.getElementById('detail');
    const { error, message, restaurant } =
      await RestofySource.detailRestaurant(url.id);

    if (!error) {
      detailContainer.innerHTML = '';

      const detailItem = document.createElement('detail-item');
      detailItem.restaurant = restaurant;
      detailContainer.appendChild(detailItem);

      const reviewContainer = document.getElementById('review-container');
      restaurant.customerReviews.map((review) => {
        const reviewItem = document.createElement('review-item');
        reviewItem.review = review;
        return reviewContainer.appendChild(reviewItem);
      });
    } else {
      detailContainer.innerHTML = `<p class='message'>${message}</p>`;
    }

    LikeButtonInitiator.init({
      likeButton: document.querySelector('#like-button'),
      restaurant,
    });

    await ReviewInitiator.init({
      form: document.querySelector('#reviewForm'),
    });
  },
};

export default Detail;
