/* eslint-disable no-trailing-spaces */
/* eslint-disable indent */
import RestofySource from '../../data/restofy-source';
import '../../component/restaurant-item';
import '../../component/testimonial-item';
import data from '../../../TESTIMONIAL.json';

const Home = {
  async render() {
    return `
      <section class="hero">
        <section class="hero__item">
          <h1 class="hero__logo">RESTOFY</h1>
          <p class="hero__title">
            Find your <br />
            Favorite restaurant 
          </p>
          <p class="hero__tagline">
            Start experiencing something that you <br />
            have never experienced before
          </p>
          <a href="#restaurant__title" class="hero__button">Start Explore</a>
        </section>
      </section>

      <section class="restaurant">
        <h2 id="restaurant__title">Explore Restaurant</h2>
        <p class="restaurant__text">Sometimes rating is true</p>

        <div id="restaurant-list">
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
          <div class="restaurant-skeleton"></div>
      </div>
      </section>

      <section class="testimonials">
        <h2 class="testimonials__title">Testimonials</h2>
        <p class="restaurant__text">What they think about this website</p>

        <div id="testimonial-list">
          <div class="testimonial-skeleton"></div>
          <div class="testimonial-skeleton"></div>
          <div class="testimonial-skeleton"></div>
        </div>
      </section>

      <footer>
        © 2022 Restofy. Made with ❤ by Helmi Yahya.
      </footer>
  `;
  },

  async afterRender() {
    const { error, message, restaurants } = await RestofySource.restaurants();
    const restaurantListContainer = document.getElementById('restaurant-list');
    const testimonialListContainer = document.getElementById('testimonial-list');

    if (!error) {
      restaurantListContainer.innerHTML = '';
      testimonialListContainer.innerHTML = '';

      restaurants.forEach((restaurant) => {
        const restaurantItem = document.createElement('restaurant-item');
        restaurantItem.restaurant = restaurant;

        restaurantListContainer.appendChild(restaurantItem);
      });

      data.testimonials.forEach((testimonial) => {
        const testimonialItem = document.createElement('testimonial-item');
        testimonialItem.testimonial = testimonial;
  
        testimonialListContainer.appendChild(testimonialItem);
      });
    } else {
      restaurantListContainer.innerHTML = `<p class='message'>${message}</p>`;
    }
  },
};

export default Home;
